		computed: {
			// 当前用户的登录状态（0:未登录，1：已登录）
			loginStatus() {
				return this.$store.state.loginStatus
			},
			
			havePhone(){
				return this.$store.state.userInfo && this.$store.state.userInfo.havePhone
			}
		},
		onShow() {
			if (!this.loginStatus || (this.loginStatus && !this.havePhone)) {
				uni.navigateTo({
					url: '../../pages/auth/auth'
				})
			}
		},
		onLoad(options) {
			this.quoteId = options.quoteId ||
				getQueryString('quoteId', decodeURIComponent(options.scene)) ||
				this.$parseURL().quoteId;
			this.state = options.state ||
				getQueryString('state', decodeURIComponent(options.scene)) ||
				this.$parseURL().state;
			
				this.refreshRequest()
			
		},
